<template>
    <div class="home commpage">
        <div class="person-top">
            <el-card class="box-card">
                <div slot="header" class="clearfix common-pfc">
                    <span>计量装置通信状态</span>
                </div>
                <div class="person-head">
                    <div>
                        <div>计量点总数</div>
                        <div class="black-color">{{personObj.total}} 个</div>
                    </div>
                    <div>
                        <div>在线数量</div>
                        <div class="green-color">{{personObj.live}} 个</div>
                    </div>
                    <div>
                        <div>离线数量</div>
                        <div class="mid-color">{{personObj.dead}} 个</div>
                    </div>
                    <div>
                        <div>当季变更次数</div>
                        <div class="mid-color">{{personObj.change}} 次</div>
                    </div>
                </div>
            </el-card>
        </div>
        <div class="common-flex">
            <div class="flex-big">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">电量总览 <i class="small-size">(kW·h）</i> </span>
                    </div>
                    <div class="card-box">
                        <div class="power-item" v-for="(item,key,index) in powerList" :key="item.label">
                            <div class="power-position" :class="item.class">
                                <div class="black-color big-size common-pfc" :title="item.value+' kW·h'">
                                    {{item.value?item.value:'-' |changeUnit}}
                                    <span class="mid-size">
                                        {{item.value>= 1000000 && item.value < 100000000?'万':item.value  > 100000000?'亿':''}}kW·h
                                    </span>
                                </div>
                                <div class="light-color mid-size">{{item.label}}</div>
                            </div>
                        </div>

                    </div>
                </el-card>
            </div>
            <div class="flex-small">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">尖峰谷平电量</span>
                        <div class="peak-select">
                            <el-select v-model="jfDay" :popper-append-to-body="false" @change="handJf">
                                <el-option v-for="item in dayOptions" :label="item.label" :key="item.label"
                                    :value="item.value"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="peak-box">
                        <div class="pers-peck-left">
                            <LineChart :class-name="'peak-pie'" id="peakChart" :char-type="'pie'"
                                :chart-data="pieCharData" />
                        </div>
                        <div class="peck-right">
                            <div class="peck-item">
                                <div class="light-color mid-size">
                                    <img class="monitor" :src="require('@/assets/img/1.png')" alt=""> 尖电量
                                    <!-- <svg-icon icon-class="ss1" class-name="monitor"></svg-icon> 尖电量 -->
                                </div>
                                <div class="black-color big-size common-pfc jfitem" :title="jfObj.jQ+' kW·h'">
                                    {{jfObj.jQ?jfObj.jQ:'-'| changeUnit}}
                                    <span class="mid-size">
                                        {{jfObj.jQ>= 1000000 && jfObj.jQ < 100000000?'万':jfObj.jQ  > 100000000?'亿':''}}kW·h
                                    </span>
                                </div>
                            </div>

                            <div class="peck-item">
                                <div class="light-color mid-size">
                                    <img class="monitor" :src="require('@/assets/img/2.png')" alt=""> 峰电量
                                </div>
                                <div class="black-color big-size common-pfc jfitem" :title="jfObj.fQ+' kW·h'">
                                    {{jfObj.fQ?jfObj.fQ:'-'| changeUnit}}
                                    <span class="mid-size">
                                        {{jfObj.fQ>= 1000000 && jfObj.fQ < 100000000?'万':jfObj.fQ  > 100000000?'亿':''}}kW·h
                                    </span>
                                </div>
                            </div>
                            <div class="peck-item">
                                <div class="light-color mid-size">
                                    <img class="monitor" :src="require('@/assets/img/3.png')" alt=""> 平电量
                                </div>
                                <div class="black-color big-size common-pfc jfitem" :title="jfObj.pQ+' kW·h'">
                                    {{jfObj.pQ?jfObj.pQ:'-'| changeUnit}}
                                    <span class="mid-size">
                                        {{jfObj.pQ>= 1000000 && jfObj.pQ < 100000000?'万':jfObj.pQ  > 100000000?'亿':''}}kW·h
                                    </span>
                                </div>
                            </div>
                            <div class="peck-item">
                                <div class="light-color mid-size">
                                    <img class="monitor" :src="require('@/assets/img/4.png')" alt=""> 谷电量
                                </div>
                                <div class="black-color big-size common-pfc jfitem" :title="jfObj.gQ+' kW·h'">
                                    {{jfObj.gQ?jfObj.gQ:'-'  | changeUnit}}
                                    <span class="mid-size">
                                        {{jfObj.gQ>= 1000000 && jfObj.gQ < 100000000?'万':jfObj.gQ  > 100000000?'亿':''}}kW·h
                                    </span>
                                </div>
                            </div>
                        </div>

                    </div>
                </el-card>
            </div>
            <div class="flex-small">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">TOP5用电量</span>
                        <div class="peak-select">
                            <el-select v-model="topDay" :popper-append-to-body="false" @change="handTop">
                                <el-option v-for="item in dayOptions" :label="item.label" :key="item.label"
                                    :value="item.value"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="card-box">
                        <LineChart :class-name="'rankChart'" :char-type="'rank'" :chart-data="rankCharData" />
                        <div class="rankBtns">
                            <span v-for="(val,key,index) in grpnames" @click="handlGroup(key)"
                                :class="rankChecked===key?'rankChecked':''">{{val}}</span>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
        <div class="common-flex">
            <div class="flex-big">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">电量趋势</span>
                        <div class="peak-select">
                            <el-select v-model="qsDay" :popper-append-to-body="false" @change="handQs">
                                <el-option v-for="item in days" :label="item.label" :key="item.label"
                                    :value="item.value"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="peak-box card-bot">
                        <LineChart :class-name="'trendChart'" :char-type="'bar'" :chart-data="trendCharData" />
                        <div class="trendTag">
                            <span>目标均值</span>
                            <span>实际均值</span>
                        </div>

                    </div>
                </el-card>
            </div>
            <div class="flex-max">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">{{typeName}}
                            <span v-if="lineName" style="font-size:14px">({{lineName}})</span>
                        </span>
                        <div class="select-boxs">
                            <div class="monitor-select">
                                <el-select filterable v-model="line" value-key="id" :popper-append-to-body="false"
                                    @change="handLine">
                                    <el-option v-for="item in lineOptions" :label="item.name" :key="item.label"
                                        :value="item"></el-option>
                                </el-select>
                            </div>
                            <div class="monitor-select">
                                <el-select v-model="type" :popper-append-to-body="false" @change="handlType">
                                    <el-option v-for="item in typeOptions" :label="item.label" :key="item.label"
                                        :value="item.value"></el-option>
                                </el-select>
                            </div>
                        </div>
                    </div>

                    <div class="card-bot">
                        <LineChart :class-name="'power'" :char-type="'line'" :chart-data="powerCharData" />
                    </div>
                </el-card>
            </div>
        </div>

    </div>
</template>

<script>
    import * as echarts from 'echarts';
    import LineChart from '@/components/char'
    import {
        mapState
    } from 'vuex'
    import moment from 'moment'
    import {
        getYwPerson,
        getDime,
        personTrend,
        getLines,
        getAnalyse
    } from '@/api/home'

    export default {
        name: 'personnel',
        components: {
            LineChart
        },
        data() {
            return {
                jfDay: 'dimYwYear',
                topDay: 'dimYwYear',
                qsDay: 'dimYwYear',
                dayOptions: [{
                    label: '日',
                    value: 'dimDay',
                }, {
                    label: '月',
                    value: 'dimYwMonth',
                }, {
                    label: '年',
                    value: 'dimYwYear',
                }],
                days: [{
                    label: '月',
                    value: 'dimYwMonth',
                }, {
                    label: '年',
                    value: 'dimYwYear',
                }],
                overAll: {}, //总览数据
                dimYwYear: '', //当年
                dimYwMonth: '', //当月
                dimDay: '', //当日
                jfObj: {}, //尖峰平谷数据
                topData: {}, //top5数据
                rankChecked: 'dim_company', //top5默认选中公司
                grpnames: {
                    'dim_company': '公司',
                    'dim_line': '线路',
                    'dim_plat': '平台'
                },
                powerList: {
                    today: {
                        class: 'powerBule',
                        value: '',
                        label: '当日电量'
                    },
                    wkzj: {
                        class: 'powerYellow',
                        value: '',
                        label: '钻井用电量'
                    },
                    yesterday: {
                        class: 'powerBule',
                        value: '',
                        label: '昨日电量'
                    },
                    wkyl: {
                        class: 'powerViolet',
                        value: '',
                        label: '压裂用电量'
                    },
                    month: {
                        class: 'powerBule',
                        value: '',
                        label: '当月电量'
                    },
                    wksc: {
                        class: 'powerRed',
                        value: '',
                        label: '生产用电量'
                    },
                    // wksq: {

                    // }
                },
                pieCharData: {
                    legend: [],
                    colors: ['#2E4AF7', '#FF8C56', '#EF4474', '#713BDB'],
                    series: [],
                    index: '',
                },
                rankCharData: {
                    series: [],
                    yAxis: [],
                    xAxis: [],
                    colors: ['#6D81FA']
                },
                trendCharData: {
                    legend: [],
                    dataRoomShow: false,
                    series: [],
                    yAxis: [],
                    xAxis: [],
                    name: ['kW·h'],
                    formatter: "用电量 <br/>{b} : {c} ",
                    colors: ['#6D81FA'],
                    mbAv: 0, //目标均值
                    sjAv: 0 //实际均值
                },
                personObj: {
                    total: '',
                    live: '',
                    dead: '',
                    change: '',
                }, //运维人员信息
                lineOptions: [], //线路
                line: null,
                typeOptions: [{
                        label: '最大需量',
                        value: 'dmdmax',
                    },
                    {
                        label: '有功负荷',
                        value: 'p',
                    }
                ], //最大需量、有功负荷
                type: 'dmdmax',
                powerCharData: {
                    legend: [],
                    xAxis: [],
                    series: [],
                    colors: ['#2E4AF7', '#FF8C56'],
                    name: ['kW'],
                },
                lineName: '',
                typeName: '最大需量',

            }
        },
        created() {
            this._getData();
        },


        computed: {
            ...mapState({
                userId: state => state.user.userId,
                jp_code: state => state.user.jp_code,
            })
        },
        mounted() {

        },
        methods: {
            //获取页面所有数据
            _getData() {
                this.dimYwYear = moment().startOf('year').format('YYYY')
                this.dimYwMonth = moment().startOf('minth').format('YYYY-MM')
                this.dimDay = moment().format('YYYY-MM-DD')
                this.getPersonInfo();
                let queryJf = {
                    dimYwYear: this.dimYwYear,
                }
                this.getDime(queryJf, 'jf')
                let queryTop = {
                    dimYwYear: this.dimYwYear,
                    top: 5,
                    grpname: this.rankChecked,
                }
                this.getDime(queryTop, 'top');
                this.personTrend()
                this.getLines()
            },
            getPersonInfo() { //运维人员信息
                getYwPerson(this.userId).then(res => {
                    this.personObj.total = res.jlzl && res.jlzl.total ? res.jlzl.total : '-'
                    this.personObj.change = res.jlzl && res.jlzl.total ? res.jlzl.change : '-'
                    this.personObj.dead = res.jlzl && res.jlzl.total ? res.jlzl.dead : '-'
                    this.personObj.live = res.jlzl && res.jlzl.total ? res.jlzl.live : '-'
                    this.overAll = res.dlzl; //总览数据
                    for (let key in this.overAll) {
                        if (this.powerList.hasOwnProperty(key)) {
                            this.powerList[key].value = this.overAll[key] && this.overAll[key].tQ ? this
                                .overAll[key].tQ : '-'
                        }

                    }
                })
            },
            getDime(query, type) { //尖峰平谷、TOP5用电量
                getDime(query).then(res => {
                    const arr = res.data ? res.data : [];
                    if (type === 'jf') {
                        const obj = {
                            jQ: arr[0].jQ,
                            fQ: arr[0].fQ,
                            pQ: arr[0].pQ,
                            gQ: arr[0].gQ,
                        }
                        this.jfObj = obj
                        this.initPieChart();
                    } else {
                        this.topData = res.data
                        this.initRankChart();
                    }
                })
            },
            personTrend() {
                let grpname = ''
                if (this.qsDay === 'dimYwYear') grpname = 'dim_yw_month'
                if (this.qsDay === 'dimYwMonth') grpname = 'dim_day'
                const query = {
                    [this.qsDay]: this[this.qsDay],
                    grpname
                }
                personTrend(query).then(res => {
                    this.trendCharData.xAxis = [];
                    this.trendCharData.yAxis = [];
                    this.trendCharData.mbAv = 0;
                    this.trendCharData.sjAv = 0;
                    this.trendCharData.series = [];
                    this.trendCharData.name[0] = 'kW·h'
                    this.trendCharData.grid = {
                        right: 24,
                        bottom: 20,
                        left: 24,
                        top: 68,
                        containLabel: true,
                    }
                    this.trendCharData.mbAv = res.mbAv;
                    this.trendCharData.sjAv = res.sjAv;
                    let w = res.list.some(item => {
                        return item.tQ >= 1000000 && item.tQ < 100000000
                    })
                    let y = res.list.some(item => {
                        return item.tQ >= 100000000
                    })

                    res.list.map((item, index) => {
                        this.trendCharData.xAxis.push(item.name)
                        let value = item.tQ;
                        if (y) {
                            value = (item.tQ / 100000000).toFixed(2)
                            if (index === 0) {
                                this.trendCharData.mbAv = (res.mbAv / 100000000).toFixed(2)
                                this.trendCharData.sjAv = (res.mbAv / 100000000).toFixed(2)
                            }

                            this.trendCharData.name[0] = '亿 kW·h'
                        } else if (w) {
                            value = (item.tQ / 10000).toFixed(2)
                            if (index === 0) {
                                this.trendCharData.mbAv = (res.mbAv / 10000).toFixed(2)
                                this.trendCharData.sjAv = (res.mbAv / 10000).toFixed(2)
                            }
                            this.trendCharData.name[0] = '万 kW·h'
                        }
                        this.trendCharData.yAxis.push(value)
                    })

                    this.trendCharData.series.push({
                        type: 'bar',
                        smooth: true,
                        data: this.trendCharData.yAxis,
                        barWidth: 12,
                        markLine: {
                            symbol: "none", //去掉警戒线最后面的箭头
                            label: {
                                position: "middle" //将警示值放在哪个位置，三个值“start”,"middle","end"  开始  中点 结束
                            },
                            data: [{
                                    type: "average",
                                    name: "目标均值",
                                    yAxis: this.trendCharData.mbAv,
                                    lineStyle: {
                                        color: '#FF8C56',
                                        type: "dashed"
                                    }
                                },
                                {
                                    name: "实际均值",
                                    yAxis: this.trendCharData.sjAv,
                                    lineStyle: {
                                        color: "#EF4474",
                                        type: "dashed"
                                    }
                                }
                            ]
                        }
                    })
                })
            },
            getLines() {
                getLines().then(res => {
                    this.lineOptions = res.data
                    this.line = res.data[0]
                    this.lineName = res.data[0].name
                    this.getAnalyse() //获取线路信息即最大需量和有功负荷
                })
            },
            handJf() {
                let queryJf = {
                    [this.jfDay]: this[this.jfDay],
                }
                this.getDime(queryJf, 'jf')
            },
            handTop() { //top5时间切换
                this.searTop()
            },
            handlGroup(group) { //点击公司、线路、平台
                this.rankChecked = group;
                this.searTop()
            },
            searTop() {
                //this.topDay当前选中的时间日、月、年， [this.topDay]根据选中的时间类型，key值变化
                let queryTop = {
                    [this.topDay]: this[this.topDay],
                    top: 5,
                    grpname: this.rankChecked,
                }
                this.getDime(queryTop, 'top')
            },
            handQs() {
                this.personTrend()
            },
            initRankChart() {
                // this.rankChart = echarts.init(document.getElementById('rankChart'));
                this.rankCharData.series = [];
                this.rankCharData.yAxis = [];
                this.rankCharData.grid = {
                    right: 120,
                    bottom: 5,
                    left: 24,
                    top: 74,
                    // containLabel: true,
                }
                const data = [];
                this.topData.sort((a, b) => { //排序
                    return a.tQ - b.tQ;
                })
                this.topData.map(item => {
                    this.rankCharData.yAxis.push(item.name);
                    data.push(item.tQ)
                })
                this.rankCharData.series = [{
                        name: '用电量',
                        type: 'bar',
                        barWidth: 12,
                        label: {
                            normal: {
                                show: true,
                                formatter: function (params) { //标签内容        
                                    let val = params.data;
                                    if (val >= 1000000 && val < 100000000) val = (val / 10000).toFixed(2) +
                                        ' 万kW·h';
                                    else if (val > 100000000) val = (val / 100000000).toFixed(2) + ' 亿kW·h';
                                    return val;
                                },
                                position: 'right',
                                valueAnimation: true,
                                fontSize: '0.18rem',
                            },
                        },
                        data: data
                    },

                ];

            },
            initPieChart() {
                let obj = this.jfObj;
                let data = [{
                    value: obj.jQ,
                    name: '尖电量'
                }, {
                    value: obj.fQ,
                    name: '峰电量'
                }, {
                    value: obj.pQ,
                    name: '平电量'
                }, {
                    value: obj.gQ,
                    name: '谷电量'
                }]
                let arr = [];
                data.map(item => {
                    arr.push(item.value)
                })
                var max = Math.max.apply(null, arr);
                this.pieCharData.index = arr.indexOf(max);
                this.pieCharData.series = [{
                    name: '尖峰平谷分析',
                    type: 'pie',
                    center: ['50%', '50%'],
                    avoidLabelOverlap: false,
                    label: {
                        show: false,
                        position: 'center'
                    },
                    emphasis: {

                        label: {
                            show: true,
                            fontSize: '20',
                            fontWeight: 'bold',
                            formatter: '{d} %',
                            color: '自适应'
                        }
                    },
                    radius: ['70%', '90%'],
                    data: data
                }]
            },
            initTrendChart() {
                let actual = 8
                // this.trendChart = echarts.init(document.getElementById('trendChart'));
                this.trendCharData.xAxis = ['周一', '周二1', '周三', '周四', '周五', '周六', '周日'];
                this.trendCharData.series = [{
                        name: '有功负荷',
                        type: 'bar',
                        smooth: true,
                        data: [10, 11, 13, 11, 12, 12, 9],
                        barWidth: 12,
                        markLine: {
                            data: [{
                                    type: "average",
                                    name: "目标均值",
                                    label: "目标均值",
                                    lineStyle: {
                                        color: '#FF8C56',
                                        type: "dashed"
                                    }
                                },
                                {
                                    label: "实际均值",
                                    name: "实际均值",
                                    yAxis: actual,
                                    lineStyle: {
                                        color: "#EF4474",
                                        type: "dashed"
                                    }
                                }
                            ]
                        }
                    },

                ]
            },
            handlType() {
                if (this.type === 'dmdmax') this.typeName = "最大需量"
                else this.typeName = '有功负荷'
                this.getAnalyse()
            },
            handLine() {

                this.lineName = this.line.name
                this.getAnalyse()
            },
            getAnalyse() {
                let query = {
                    gLine: this.line.code,
                    index: this.type
                }
                getAnalyse(query).then(res => {
                    this.powerCharData.xAxis = []
                    this.powerCharData.series = []
                    const data = []
                    res.data && res.data.list ? res.data.list.map(item => {
                        this.powerCharData.xAxis.push(item.date)
                        data.push(item.value)
                    }) : ''
                    if (res.data && res.data.list > 0) {
                        this.pieCharData.name = ['kW']
                    } else {
                        this.pieCharData.name = []
                    }
                    this.powerCharData.grid = {
                        right: 24,
                        bottom: 20,
                        left: 24,
                        top: 68,
                        containLabel: true,
                    }
                    this.powerCharData.series.push({
                        name: this.lineName,
                        symbol: "none",
                        type: 'line',
                        itemStyle: {
                            normal: {
                                lineStyle: {
                                    width: 3 //设置线条粗细
                                }
                            }
                        },
                        areaStyle: {
                            opacity: 0.2,
                            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                                    offset: 0,
                                    color: '#d1e9fe'
                                },
                                {
                                    offset: 1,
                                    color: '#5269f8'
                                }
                            ])
                        },
                        smooth: true,
                        data: data,
                    })

                })
            },
        },
        //keepalive 加载组件事件 再次加载数据
        activated: function () {
            this._getData();
        },
        //keepalive 离开组件事件，清空数据
        deactivated: function () {
            this.powerCharData.series = [];
            this.trendCharData.series = [];
            this.topData = [];
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import "./common.scss";

    .person-top {
        margin-bottom: 0.24rem;
    }

    .person-head {
        padding: 0.21rem 0 0.4rem 0.24rem;
